/* NAV */
nav {
	/*color: #f1f1f1;*/
	display:inline-block;
	margin-top:1em;
	padding:.25em 0;
}

nav .navLink {
  float: left;
  display: block;
  color: #eeeeee;
  text-align: center;
  padding: 11px 11px;
  text-decoration: none;
  font-size: 17px;
}
nav label {
	border: .15em solid #393e46;
	border-radius: 10px;
	margin-left: 2em;
}
nav label:hover {
  background-color: #393e46;
}
nav li, nav label {
	display:inline-block;
	-moz-transition:background .3s;
	-webkit-transition:background .3s;
	transition:background .3s;
}
#nav-input {
	display: none;
}
@media all and (min-width: 48em) {
	nav:before {
		position:absolute;
		top:0; right:100%; bottom:0;
		content:"";
	}
	nav li {
		margin-right:2rem;
	}
	nav li:hover {
		background: #393e46;
	}
	#nav-unicode, label[for="nav-input"] {
		display: none;
	}
}
@media all and (max-width: 48em) {
	nav {
		text-align: left;
		width: 100%;
	}
	nav.fixed {
		text-align:left;
	}
	nav.fixed ul {
		box-shadow: none;
	}

	nav li {
		text-align: center;
		width: 100%;
		float: none;
	}
	nav li:hover {
		background:#393e46;
	}
	#nav-unicode, label[for="nav-input"] {
		cursor: pointer;
		display: inline-block;
	}
	label[for="nav-input"] {
		padding: .5em 1em;
	}
	#nav-input + ul {
		display: none;
	}
	#nav-input:checked + ul {
		position: absolute;
		top: 3.25em; left: 0; right: 0;
		display: block;
		z-index: 9;
	}
}
/* FIN NAV */

